import logo from './logo.svg';
import './App.css';

function App() {
  return (
    <div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p className="App-text">
          <code>{dec2binary(5)}.{dec2binary(10)}</code>
        </p>
        <input></input>
        <button>Hi There</button>
      </header>
    </div>
  );
}

function dec2binary(number) {
  var binary = "";
  if (number >= 32) {
    binary += '1';
    number = number - 32;
  } else {
    binary += '0'
  }
  if (number >= 16) {
    binary += '1';
    number = number - 16;
  } else {
    binary += '0'
  }
  if (number >= 8) {
    binary += '1';
    number = number - 8;
  } else {
    binary += 0
  }
  if (number >= 4) {
    binary += '1';
    number = number - 4;
  } else {
    binary += 0
  }
  if (number >= 2) {
    binary += '1';
    number = number - 2;
  } else {
    binary += 0
  }
  if (number >= 1) {
    binary += '1';
  } else {
    binary += 0
  }
  return binary
}

export default App;
